<template>
  <v-dialog v-model="dialog" max-width="1200px">
    <v-card>
      <v-card-title>
        <div class="display-1 card-title">{{ title }}</div>
        <div class="title card-date">{{ date }}</div>
      </v-card-title>
      <v-container grid-list-lg>
        <v-layout wrap>
          <v-flex xs12 md6>
            <v-responsive :aspect-ratio="1 / 1">
              <v-carousel
                height="100%"
                hide-delimiters
                :show-arrows="images.length !== 1"
              >
                <v-carousel-item
                  v-for="(image, i) in images"
                  :key="i"
                  :src="image"
                ></v-carousel-item>
              </v-carousel>
            </v-responsive>
          </v-flex>
          <v-flex xs12 md6>
            <div class="work-detail">
              <div>
                <div class="title">Description</div>
                <div class="detail-content">
                  <span class="subheading" v-html="description"></span>
                </div>
              </div>
              <div>
                <div class="title" style="padding-top: 20px">Technologies</div>
                <ul class="detail-content">
                  <li
                    class="subheading"
                    v-for="(technology, i) in technologies"
                    :key="i"
                  >
                    {{ technology }}
                  </li>
                </ul>
              </div>
              <div class="source">
                <source-link
                  v-if="source != null"
                  :address="source"
                ></source-link>
              </div>
            </div>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
import sourceLink from './sourceLink.vue'

export default {
  components: {
    sourceLink,
  },
  props: {
    title: String,
    date: String,
    description: String,
    technologies: Array,
    source: String,
    images: Array,
  },
  data() {
    return {
      dialog: true,
    }
  },
  watch: {
    dialog(val) {
      if (!val) {
        this.$router.push('/works')
        this.$emit('close')
      }
    },
  },
}
</script>

<style scoped>
.card-title {
  width: 100%;
  text-align: center;
}
.card-date {
  width: 100%;
  text-align: center;
  padding-top: 10px;
}
.work-detail {
  padding: 0 20px;
}
.detail-content {
  margin: 10px 0;
}
.source {
  text-align: right;
}
</style>
