<template>
  <v-layout justify-center align-center>
    <v-flex xs12 xl8>
      <v-container fluid grid-list-xl>
        <v-layout row wrap>
          <v-flex v-for="work in workArray" :key="work.title">
            <v-card
              hover
              tile
              @click="openDetail(work.title)"
              height="350px"
              width="250px"
              style="margin: 0 auto"
            >
              <v-img
                aspect-ratio="1"
                class="grey lighten-2"
                :src="work.eyeCatchImage"
                width="250px"
              ></v-img>

              <v-card-text>
                <div>
                  <v-icon small style="padding-bottom: 5px"
                    >far fa-calendar</v-icon
                  >
                  <span class="subtitle-2" style="padding-left: 5px">{{
                    work.date
                  }}</span>
                </div>
                <div class="work-title">{{ work.title }}</div>
              </v-card-text>
            </v-card>
            <router-view
              v-if="openedWork === work.title"
              :title="work.title"
              :date="work.date"
              :description="work.description"
              :technologies="work.technologies"
              :source="work.source"
              :images="work.images"
              @close="closeDetail"
            ></router-view>
          </v-flex>
        </v-layout>
      </v-container>
    </v-flex>
  </v-layout>
</template>

<script>
import firebase from '../firebaseConfig'
import { getFirestore } from "firebase/firestore";
import { collection, getDocs } from "firebase/firestore"; 
export default {
  name: 'Works',
  data: function() {
    return {
      workArray: [],
      openedWork: null,
    }
  },
  async created() {
    const db = getFirestore(firebase)
    try {
      const works = await getDocs(collection(db, 'works'))
      works.forEach(work => {
        this.workArray.push(work.data())
      })
      this.workArray.sort((a, b) => {
        return Date.parse(b.date) - Date.parse(a.date)
      })
    } catch (err) {
      console.log(err)
    }

    this.setOpenedWorkFromPath(this.$route.path)
  },
  methods: {
    openDetail(title) {
      this.openedWork = title
      this.$router.push('/works/' + title)
    },
    closeDetail() {
      this.openedWork = null
    },
    setOpenedWorkFromPath(path) {
      // /works/portfolio -> '', 'works', 'portfolio
      const pathArray = path.split('/')
      if (pathArray.length === 3) {
        this.openedWork = pathArray[2]
      } else {
        this.openedWork = null
      }
    },
  },
  watch: {
    $route(to, from) {
      this.setOpenedWorkFromPath(to.path)
    },
  },
}
</script>

<style scoped>
.work-title {
  width: 100%;
  text-align: center;
  font-size: large;
  padding-top: 10px;
}
.work-date {
  margin-left: auto;
}
</style>
