<template>
  <div>
    <v-alert v-if="displaySuccess" type="success">
      追加成功
    </v-alert>
    <v-alert v-if="displayError" type="error">
      追加失敗
    </v-alert>
    <v-overlay :value="overlay">
      <v-icon>fas fa-feather</v-icon>
      追加中
    </v-overlay>
    <div>
      <v-text-field type="text" placeholder="Username" v-model="username" />
      <v-text-field type="password" placeholder="Password" v-model="password" />
      <v-btn color="success" class="mr-4" @click="signIn">
        Signin
      </v-btn>
    </div>
    <v-form v-if="loginSuccess" v-model="valid" ref="form">
      <v-container>
        <v-text-field v-model="date" label="Date" :rules="textRule" required>
        </v-text-field>
        <v-text-field v-model="title" label="Title" :rules="textRule" required>
        </v-text-field>
        <v-text-field
          v-model="documentName"
          label="documentName(English)"
          :rules="textRule"
          required
        >
        </v-text-field>
        <v-text-field
          v-model="description"
          label="Description"
          :rules="textRule"
          required
        >
        </v-text-field>
        <v-combobox
          v-model="technologies"
          label="technologies"
          multiple
        ></v-combobox>

        <!-- アイキャッチ画像アップローダ -->
        <v-file-input
          label="アイキャッチ画像"
          prepend-icon="mdi-camera"
          v-model="eyecatch"
          @change="loadEyecatch"
        ></v-file-input>
        <vue-cropper
          v-if="eyecatch"
          ref="eyecatch"
          alt="Source Image"
          :aspect-ratio="1 / 1"
          :view-mode="1"
          :zoom-on-wheel="false"
        ></vue-cropper>

        <!-- 写真アップローダ -->
        <v-file-input
          label="写真"
          multiple
          prepend-icon="mdi-camera"
          v-model="photos"
          :rules="photoRules"
          @change="loadPhotos"
        ></v-file-input>
        <vue-cropper
          v-for="(cropper, index) in photos"
          ref="cropper"
          alt="Source Image"
          :aspect-ratio="1 / 1"
          :view-mode="1"
          :zoom-on-wheel="false"
          :key="index"
        ></vue-cropper>
      </v-container>
      <v-btn :disabled="!valid" color="success" class="mr-4" @click="post">
        新規追加
      </v-btn>
    </v-form>
  </div>
</template>

<script>
import firebase from '../firebaseConfig'
import VueCropper from 'vue-cropperjs'
import 'cropperjs/dist/cropper.css'
import { getFirestore } from "firebase/firestore";
import { collection, addDoc } from "firebase/firestore"; 
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { getStorage, ref, uploadBytes, getDownloadURL } from "firebase/storage";

export default {
  components: { VueCropper },
  data() {
    return {
      username: '',
      password: '',
      loginSuccess: false,
      overlay: false,

      valid: false,
      date: '',
      title: '',
      documentName: '',
      description: '',
      technologies: [],
      eyecatch: null,
      photos: [],

      textRule: [v => !!v || '必須項目'],
      photoRules: [v => v.length !== 0 || '写真の投稿は必須です'],

      displaySuccess: false,
      displayError: false,
    }
  },
  methods: {
    async signIn() {
      const auth = getAuth();
      signInWithEmailAndPassword(auth, this.username, this.password)
      .then((userCredential) => {
        this.loginSuccess = true
      })
      .catch((error) => {
        console.log(error)
      })
    },
    async post() {
      this.overlay = true
      this.displaySuccess = false
      this.displayError = false

      // firestorageへの写真のアップロード
      const storage = getStorage();

      // アイキャッチ画像
      let eyeCatchUrl = null
      const eyecatchRef = ref(storage, `works/${this.title}/eyecatch.png`)
      // 500x500pxにリサイズする
      const file = await this.getCanvasBlob(
        this.resizeImg(this.$refs.eyecatch.getCroppedCanvas(), 500, 500),
      )

      try {
        await uploadBytes(eyecatchRef, file)
        eyeCatchUrl = await getDownloadURL(eyecatchRef)
      } catch (err) {
        this.displayError = true
        this.overlay = false
        return
      }

      // 詳細画像
      const imagesUrl = []
      for (let i = 0; i < this.photos.length; i++) {
        const imageRef = ref(storage, `works/${this.title}/figure${i}.png`)
        // 800x800pxにリサイズする
        const file = await this.getCanvasBlob(
          this.resizeImg(this.$refs.cropper[i].getCroppedCanvas(), 800, 800),
        )
        try {
          await uploadBytes(imageRef, file)
          imagesUrl.push(await getDownloadURL(imageRef))
          console.log(imagesUrl)
        } catch (err) {
          this.displayError = true
          this.overlay = false
          return
        }
      }

      // firestoreへの情報の登録
      const db = getFirestore(firebase)
      try {
        await addDoc(collection(db, 'works'), {
            date: this.date,
            title: this.title,
            description: this.description,
            technologies: this.technologies,
            eyeCatchImage: eyeCatchUrl,
            images: imagesUrl,
          })
        this.displaySuccess = true
        this.overlay = false
      } catch (err) {
        this.displayError = true
        this.overlay = false
      }
    },
    resizeImg(originalCanvas, width, height) {
      const canvas = document.createElement('canvas')
      canvas.width = width
      canvas.height = height
      const ctx = canvas.getContext('2d')
      ctx.drawImage(originalCanvas, 0, 0, width, height)
      return canvas
    },
    async getCanvasBlob(canvas) {
      return new Promise((resolve, reject) => {
        canvas.toBlob(function(blob) {
          resolve(blob)
        })
      })
    },
    loadEyecatch() {
      if (typeof FileReader !== 'function') {
        alert('Sorry, FileReader API not supported')
      }
      const file = this.eyecatch
      const reader = new FileReader()
      reader.onload = event => {
        this.$refs.eyecatch.replace(event.target.result)
      }
      reader.readAsDataURL(file)
    },
    loadPhotos() {
      if (typeof FileReader !== 'function') {
        alert('Sorry, FileReader API not supported')
      }
      for (let i = 0; i < this.photos.length; i++) {
        const file = this.photos[i]
        const reader = new FileReader()
        reader.onload = event => {
          this.$refs.cropper[i].replace(event.target.result)
        }
        reader.readAsDataURL(file)
      }
    },
  },
}
</script>

